body {
  background-color: #080C20;
  color: #FFFFFF;
  font-family: "Manrope", sans-serif;
  font-optical-sizing: auto;
}

nav {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  nav {
    justify-content: flex-start;
    align-items: center;
    height: 160px;
  }
}
@media screen and (min-width: 1110px) {
  nav {
    margin-right: 165px;
    height: 225px;
  }
}

.logo {
  padding: 2rem 0rem;
  height: 35px;
}
@media screen and (min-width: 768px) {
  .logo {
    margin-left: 39px;
    padding: 0;
  }
}

h1 {
  text-align: center;
  font-size: 32px;
}
@media screen and (min-width: 768px) {
  h1 {
    font-size: 48px;
    margin: 0;
  }
}

h2 {
  text-align: center;
  font-size: 24px;
}

.green {
  color: #3EE9E5;
}

.subheading-text {
  color: #777F98;
  text-align: center;
  font-size: 15px;
}

.hero__mobile-img {
  height: 360px;
  width: 100%;
  object-fit: cover;
  object-position: left;
  margin: 2rem 0rem;
}
@media screen and (min-width: 768px) {
  .hero__mobile-img {
    display: none;
  }
}
.hero__container {
  display: grid;
  align-items: center;
  justify-items: center;
  position: relative;
}
@media screen and (min-width: 768px) {
  .hero__container {
    grid-template-columns: repeat(3, 1fr);
  }
}
.hero__container--middle {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__text {
  color: #777F98;
  width: 325px;
  text-align: center;
  font-size: 15px;
  line-height: 25px;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media screen and (min-width: 768px) {
  .hero__text {
    margin-top: 2.25rem;
    margin-bottom: 2.25rem;
  }
}
.hero__img {
  display: none;
}
@media screen and (min-width: 768px) {
  .hero__img {
    display: block;
    object-fit: cover;
    width: 100%;
    height: 100%;
    max-height: 450px;
  }
}
.hero__bg-squiggle {
  display: none;
}
@media screen and (min-width: 768px) {
  .hero__bg-squiggle {
    display: block;
    position: absolute;
    z-index: -1;
    object-fit: cover;
  }
}

.spacer {
  height: 80px;
}

.illustration-container {
  background-color: #093F68;
  width: 120px;
  height: 124px;
  border-radius: 45px;
  position: relative;
}
@media screen and (min-width: 768px) {
  .illustration-container {
    width: 160px;
    height: 164px;
  }
}
@media screen and (min-width: 768px) {
  .illustration-container {
    margin-right: 55px;
  }
}

.illustration {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services {
  margin: 0 auto;
  margin-left: 1rem;
  margin-right: 1rem;
}
@media screen and (min-width: 768px) {
  .services {
    margin-left: 2.25rem;
    margin-right: 2.25rem;
  }
}
@media screen and (min-width: 1110px) {
  .services {
    margin: auto;
    max-width: 1110px;
  }
}
.services__text-container {
  text-align: center;
}
.services__box {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .services__box {
    max-width: 700px;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1110px) {
  .services__box {
    flex-direction: row;
    min-height: 380px;
    margin: 4rem 1rem;
    max-width: 100%;
  }
}
.services__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 40px 0;
}
@media screen and (min-width: 768px) {
  .services__container {
    flex-direction: row;
    align-items: flex-start;
    margin: 40px 30px;
  }
  .services__container:nth-child(1) {
    justify-content: flex-start;
  }
  .services__container:nth-child(2) {
    justify-content: flex-end;
  }
  .services__container:nth-child(3) {
    justify-content: flex-start;
  }
  .services__container:nth-child(4) {
    justify-content: flex-end;
  }
}
@media screen and (min-width: 1110px) {
  .services__container {
    flex-direction: column;
    margin: 0 30px;
  }
}
@media screen and (min-width: 768px) {
  .services__heading {
    text-align: left;
  }
}
.services__text {
  color: var(--secondary-bg-grey);
  font-size: 15px;
  line-height: 28px;
}
@media screen and (min-width: 768px) {
  .services__text {
    text-align: left;
    max-width: 340px;
  }
}

.pp {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  margin-right: 1rem;
}
@media screen and (min-width: 768px) {
  .pp {
    margin-left: 2.25rem;
    margin-right: 2.25rem;
  }
}
@media screen and (min-width: 1110px) {
  .pp {
    margin: auto;
    max-width: 1110px;
  }
}
@media screen and (min-width: 768px) {
  .pp {
    flex-direction: row;
    align-items: center;
  }
}
.pp__container {
  position: relative;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-radius: 8px;
}
@media screen and (min-width: 768px) {
  .pp__container {
    margin-top: 2.25rem;
    margin-bottom: 2.25rem;
  }
}
.pp__container:nth-child(1) {
  background-color: #093F68;
}
@media screen and (min-width: 768px) {
  .pp__container:nth-child(1) {
    margin-right: 1.5rem;
    max-height: 501px;
  }
}
@media screen and (min-width: 1110px) {
  .pp__container:nth-child(1) {
    margin: 2rem 1.5rem;
  }
}
.pp__container:nth-child(2) {
  background-color: #3EE9E5;
  color: #093F68;
}
@media screen and (min-width: 768px) {
  .pp__container:nth-child(2) {
    margin-left: 1.5rem;
    height: 528px;
    padding-top: 24px;
  }
}
@media screen and (min-width: 1110px) {
  .pp__container:nth-child(2) {
    margin: 2rem 1.5rem;
  }
}
.pp__container--wrapper {
  margin-left: 1rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.pp__icon {
  position: absolute;
  top: -23px;
  left: 32px;
}
.pp__heading {
  margin-top: 4rem;
  font-size: 18px;
}
.pp__heading--2 {
  font-size: 40px;
}
.pp__text {
  color: #777F98;
  font-size: 15px;
  line-height: 25px;
}
.pp__list {
  padding-left: 28px;
  padding-bottom: 1rem;
}

li {
  margin-bottom: 12px;
  position: relative;
}

.list-icon {
  position: absolute;
  left: -28px;
  top: 6px;
  height: 11px;
}

ul {
  font-size: 15px;
  list-style: none;
}

.month {
  color: #1d646f;
  font-size: 15px;
  font-weight: 400;
  vertical-align: 8px;
}

.light-navy {
  color: #1d646f;
}

.not {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
  margin-right: 1rem;
  height: 400px;
}
@media screen and (min-width: 768px) {
  .not {
    margin-left: 2.25rem;
    margin-right: 2.25rem;
  }
}
@media screen and (min-width: 1110px) {
  .not {
    margin: auto;
    max-width: 1110px;
  }
}
.not__bg {
  width: 100vw;
  height: 228px;
  position: absolute;
  z-index: -1;
}
.not__container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .not__container {
    flex-direction: row;
    width: 560px;
  }
}
.not__email {
  position: relative;
  outline: none;
  background-color: #093F68;
  border: 1px solid #080C20;
  padding: 12px 18px;
  border-radius: 24px;
  font-size: 15px;
  font-weight: 600;
  color: #777F98;
  margin-bottom: 24px;
  width: 100%;
  box-sizing: border-box;
}
@media screen and (min-width: 768px) {
  .not__email {
    width: 320px;
    margin-bottom: 0;
  }
}
.not__email:focus-visible {
  border: 1px solid #3EE9E5;
  color: #FFFFFF;
}
.not__button {
  background-color: #3EE9E5;
  border: 1px solid #080C20;
  padding: 12px 18px;
  border-radius: 24px;
  font-size: 15px;
  color: #080C20;
  width: 100%;
  margin: 0 8px;
}
.not__button:hover {
  border: 1px solid #3EE9E5;
  color: #FFFFFF;
  background-color: #080C20;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .not__button {
    width: 140px;
  }
}

.error-text {
  font-size: 12px;
  color: #FF2965;
  position: absolute;
  left: 20px;
}
@media screen and (min-width: 768px) {
  .error-text {
    left: 62px;
    top: 50px;
  }
}

.invalid {
  border: 1px solid #FF2965;
}

.hidden {
  display: none;
}

@keyframes an-ms {
  0% {
    opacity: 1;
    top: 5%;
  }
  15% {
    opacity: 1;
    top: 30%;
  }
  50% {
    opacity: 0;
    top: 30%;
  }
  100% {
    opacity: 0;
    top: 5%;
  }
}
.scroll-bttn {
  width: 22px;
  height: 38px;
  border: 2px solid #3EE9E5;
  border-top-right-radius: 28px;
  border-top-left-radius: 28px;
  border-bottom-right-radius: 28px;
  border-bottom-left-radius: 28px;
  cursor: pointer;
  position: relative;
}

.scroll-dot {
  display: block;
  position: absolute;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  border: 2px solid #3EE9E5;
  left: 7.5px;
  animation: an-ms 3s linear infinite;
}